<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-c-scale-to-original"></i> 课程设计
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container"  v-loading="loading">
            <div class="form">
                <div class="form-top">
                    <el-radio-group size="small" @change="changetab" v-model="choosed">
                      <el-radio-button label="未完成课程设计"></el-radio-button>
                      <el-radio-button label="已完成课程设计"></el-radio-button>
                    </el-radio-group>
                </div>
                <div class="cont">
                    <div class="cont-one" v-for="item in items">
                        <div class="one-left">
                            <h2>{{item.workTeachingDTO.name}}</h2>
                            <p class="stu-num"><strong>指导老师：</strong>{{item.workTeachingDTO.teacherName}}</p>
                            <p class="work-time"><strong>作业提交：</strong>{{item.workTeachingDTO.deadlineTime}} 之前</p>
                            <p class="status"><strong>教师留言：</strong>{{item.workTeachingDTO.teacherReminder}}</p>
                        </div>
                        <div class="one-right">
                            <p><el-button type="primary" size="mini" @click="openLink(item.workId)" plain>进入作业</el-button></p>
                        </div>
                    </div>

                    <div class="pagination">
                          <el-pagination
                            background
                            :page-size="pageSize"
                            @current-change="changePage"
                            layout="prev, pager, next"
                            :total="total">
                          </el-pagination>
                      </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {fetchGetworklistbystudentid} from '../../axios/requests'
export default {
    name: 'course',
    data() {
        return {
            choosed:'未完成课程设计',
            items:[],

            loading:false,
            pageNum:1,
            pageSize:10,
            total:0,
        };
    },
    mounted(){
        this.Getworklistbystudentid()
    },
    methods: {
        openLink(id){
            this.$openBlank('Tstuwork',{id});
        },
        changetab(e){
            this.pageNum = 1;
            this.Getworklistbystudentid();
        },
        changePage(page){
          this.pageNum = page;
          this.Getworklistbystudentid();
        },
        async Getworklistbystudentid() {//得到教学列表
          this.loading = true;
          let {pageNum, pageSize, choosed} = this;
          var status = choosed=="未完成课程设计"?0:1;
          try {
            let response = await fetchGetworklistbystudentid(status, pageNum, pageSize);
            if (response.status.code ==0) {
              this.total = response.result.total;
              this.items = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
    }
};
</script>
<style lang='scss' scoped>
    .cont-one{
        display:flex;
        padding:15px 20px;
        border:1px solid #ccc;
        margin-top:20px;
        .one-left{
            flex:1;
            font-size:14px;
            min-width:0;
            line-height:24px;
            color:#666;
            strong{
                font-weight:500;
                color:#333;
            }
            h2{
                font-size:16px;
                line-height:28px;
            }
            .status{
                overflow: hidden;
                width:100%;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 1;
            }
        }
        .one-right{
            width:90px;
            padding-top:61px;
            p{
                margin-top:10px;
            }
            button{
                text-align: right;
                display:block;
                margin-left:10px;
            }
        }
    }
    .pagination{
        margin-top:20px;
    }
</style>


